export function setActiveKeyListItem(items, item) {
    items.forEach(i => (i.tabIndex = -1));
    item.tabIndex = 0;
}
// todo cory: test
export function initializeKeyListItems(items) {
    items.forEach(i => (i.tabIndex = -1));
    items[0].tabIndex = 0;
}
